/* Data Table Background Title Row */
.table th {
	background-color: #3c8dbc;
	color: white;
}

/* Data Table Row Mouseover color */
.table tbody tr:hover td {
	background-color: #b3d7ec;
}


/* Reduce padding to make rows shorter */
table.table-bordered.dataTable tbody td {
    padding: 4px;

}


